      color: rgb(64 64 64 / var(--tw-text-opacity, 1));
    }

    .lg\:dark\:hover\:text-white:hover {
      --tw-text-opacity: 1;
      color: rgb(255 255 255 / var(--tw-text-opacity, 1));
    }
  }
}
